import 'bootstrap';
import 'bootstrap/dist/css/bootstrap.css';
import 'react-calendar/dist/Calendar.css';
import React, {useEffect, useState} from 'react';
import {render} from 'react-dom';
import {Parser} from 'html-to-react';
import moment from 'moment';
import axios from 'axios';
import Calendar from 'react-calendar';
import './index.less';

const HtmlParser = new Parser();
const App = () => {
    const [program, setProgram] = useState('');
    const [calendarDate, onCalendarDate] = useState(new Date());
    useEffect(() => {
        onDateChange(calendarDate);
    }, []);

    const onDateChange = (date) => {
        onCalendarDate(date);
        loadWorkout(moment(date));
    }

    const loadWorkout = (newDate) => {
        axios.get(`/wods/programs/${newDate.format('YYYY-MM-DD')}.html`).then(response => {
            setProgram(response.data);
        });
    };


    return <div className="container-fluid">
        <div className="row">
            <div className="col-3 calendar">
                <div className="card">
                    <div className="card-body">
                        <Calendar
                            minDate={moment('2020-11-27').toDate()}
                            maxDate={moment().toDate()}
                            onChange={onDateChange}
                            value={calendarDate}
                        />
                    </div>
                </div>
            </div>
            <div className="col-9 program">
                <div className="card">
                    <div className="card-header">
                        {moment(calendarDate).format('dddd DD.MM.YYYY')}
                    </div>
                    <div className="card-body">
                        {HtmlParser.parse(program)}
                    </div>
                </div>
            </div>
        </div>
    </div>;
};

render(<App/>, document.getElementById('root'));
